.animation-s1{
	
	/* Zoom In #1 */
	.group-banners{
		.banner-image img {
			-webkit-transform: scale(1);
			transform: scale(1);
			-webkit-transition: .8s ease-in-out;
			transition: .8s ease-in-out;
		}
		.banner-image:hover img {
			-webkit-transform: scale(1.1);
			transform: scale(1.1);
		}
	}
	.pro-main{
		
		.banner-image img {
			-webkit-transform: scale(1);
			transform: scale(1);
			-webkit-transition: .8s ease-in-out;
			transition: .8s ease-in-out;
		}
		.banner-image:hover img {
			-webkit-transform: scale(1.1);
			transform: scale(1.1);
		}
	}

	.banner-category img {
		-webkit-transform: scale(1);
		transform: scale(1);
		-webkit-transition: .8s ease-in-out;
		transition: .8s ease-in-out;
	}
	.banner-category:hover img {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}

	.full-width-banner{
		.banner-image img {
			-webkit-transform: scale(1);
			transform: scale(1);
			-webkit-transition: .3s ease-in-out;
			transition: .3s ease-in-out;
		}
		.banner-image:hover img {
			-webkit-transform: scale(1.2);
			transform: scale(1.2);
		}
	}
	
	.full-screen-banner{
		.banner-image img {
			-webkit-transform: scale(1);
			transform: scale(1);
			-webkit-transition: .3s ease-in-out;
			transition: .3s ease-in-out;
		}
		.banner-image:hover img {
			-webkit-transform: scale(1.2);
			transform: scale(1.2);
		}
	}
	.cat-thumb{
		overflow: hidden;
		position: relative;
	}
	.cat-thumb img {
		-webkit-transform: scale(1);
			transform: scale(1);
			-webkit-transition: .3s ease-in-out;
			transition: .3s ease-in-out;
	}
	.cat-thumb:hover img {
		-webkit-transform: scale(1.2);
			transform: scale(1.2);
	}
		
}

.animation-s2{
	/* Flashing */

	.group-banners{
		.banner-image:hover img {
			opacity: 1;
			-webkit-animation: flash 1.5s;
			animation: flash 1.5s;
		}
		
	}

	.full-width-banner{
		.banner-image:hover img {
			opacity: 1;
			-webkit-animation: flash 1.5s;
			animation: flash 1.5s;
		}
		
	}
	.full-screen-banner{
		.banner-image:hover img {
			opacity: 1;
			-webkit-animation: flash 1.5s;
			animation: flash 1.5s;
		}
		
	}

	.cat-thumb:hover img {
		opacity: 1;
		-webkit-animation: flash 1.5s;
		animation: flash 1.5s;
	}
	
	
	 

}

@-webkit-keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}
@keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}

.animation-s3{
	/* Shine */
	
	.group-banners{
		.banner-image{
			position: relative;
		}
		.banner-image::before {
			position: absolute;
			top: 0;
			left: -95%;
			z-index: 2;
			display: block;
			content: '';
			width: 50%;
			height: 100%;
			background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
			background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
			-webkit-transform: skewX(-25deg);
			transform: skewX(-25deg);
		}
		.banner-image:hover::before {
			-webkit-animation: shine .95s;
			animation: shine .95s;
		}
		
		
	}

	.full-width-banner{
		.banner-image{
			position: relative;
		}
		.banner-image::before {
			position: absolute;
			top: 0;
			left: -75%;
