.animation-s4{
	/* Circle */

	.group-banners{
		.banner-image {
			position: relative;
		}
		.banner-image::before {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 2;
			display: block;
			content: '';
			width: 0;
			height: 0;
			background: rgba(255,255,255,.2);
			border-radius: 100%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			opacity: 0;
		}
		.banner-image:hover::before {
			-webkit-animation: circle 1s;
			animation: circle 1s;
		}
		
		
	}

	.full-width-banner{
